<template >
  <div class="desktop-new-user">
    <!-- Slider main container -->
    <div class="swiper-container">
      <!-- Additional required wrapper -->
      <div class="swiper-wrapper">
        <!-- Slides -->
        <div class="swiper-slide desktop-new-user__slide">
          <!-- TEXT WRAPPER -->
          <div class="desktop-new-user__slide__text-wrapper">
            <h1>Starting your career has never been so easy</h1>
            <p
              class="desktop-new-user__slide__text-wrapper__p"
            >All graduate and intern opportunities are now in one place</p>
            <div class="desktop-new-user__slide__text-wrapper__input-wrapper">
              <div class="desktop-new-user__slide__text-wrapper__input-wrapper__radio-wrapper">
                <div
                  class="desktop-new-user__slide__text-wrapper__input-wrapper__selector graduate-selector"
                  ref="jobTypeSelector"
                ></div>
                <div
                  class="desktop-new-user__slide__text-wrapper__input-wrapper__option desktop-new-user__slide__text-wrapper__input-wrapper__option__1"
                  ref="option1"
                >
                  <input
                    type="radio"
                    name="job-type"
                    id="grad-type"
                    class="desktop-new-user__slide__text-wrapper__input-wrapper__option__radio-btn"
                    value="graduate"
                    v-model="jobType"
                    v-on:change="updateJobSelector"
                    checked
                  />
                  <p class ref="pGraduate">Graduate</p>
                </div>
                <div
                  class="desktop-new-user__slide__text-wrapper__input-wrapper__option desktop-new-user__slide__text-wrapper__input-wrapper__option__2"
                  ref="option2"
                >
                  <input
                    type="radio"
                    name="job-type"
                    id="intern-type"
                    class="desktop-new-user__slide__text-wrapper__input-wrapper__option__radio-btn"
                    value="intern"
                    v-model="jobType"
                    v-on:change="updateJobSelector"
                  />
                  <p class="p-blue" ref="pIntern">Intern</p>
                </div>
              </div>
            </div>

            <div class="desktop-new-user__slide__text-wrapper__input-wrapper__search-wrapper">
              <div
                class="desktop-new-user__slide__text-wrapper__input-wrapper__search-wrapper__input-box"
              >
                <label for="inputIndustry" ref="industryLabel">e.g. Job title, Industry, Company</label>
                <input
                  type="text"
                  class="desktop-new-user__slide__text-wrapper__input-wrapper__search-wrapper__input-box__input-search"
                  id="inputIndustry"
                  placeholder="What are you looking for?"
                  ref="inputIndustry"
                  @input="showLabel('industry')"
                  v-model="inputIndustry"
                />
              </div>
              <div
                class="desktop-new-user__slide__text-wrapper__input-wrapper__search-wrapper__input-box"
              >
                <label for="inputLocation" ref="locationLabel">e.g. London, North West England</label>
                <input
                  type="text"
                  class="desktop-new-user__slide__text-wrapper__input-wrapper__search-wrapper__input-box__input-search"
                  id="inputLocation"
                  placeholder="Where would you like to work?"
                  ref="inputLocation"
                  @input="showLabel('location')"
                  v-model="inputLocation"
                />
                <i class="fa fa-map-pin fa-3x compass-icon"></i>
              </div>
              <div
                class="desktop-new-user__slide__text-wrapper__input-wrapper__search-wrapper__input-box"
              >
                <button
                  class="desktop-new-user__slide__text-wrapper__input-wrapper__search-wrapper__input-box__btn-search"
                >
                  <p>Search</p>
                </button>
              </div>
            </div>
          </div>
          <!-- IMG WRAPPER -->
          <div class="desktop-new-user__slide__img-wrapper">
            <img src="../../../assets/SVG/click_here.png" alt="svgg" style="width: 500px" />
          </div>
        </div>
        <div class="swiper-slide desktop-new-user__slide">Slide 2</div>
        <div class="swiper-slide desktop-new-user__slide">Slide 3</div>
        <div class="swiper-slide desktop-new-user__slide">Slide 4</div>
      </div>

      <div class="swiper-pagination"></div>
      <div class="swiper-scrollbar"></div>

      <!-- If we need navigation buttons -->
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
    </div>
  </div>
</template>



<script>
import "../../../assets/SVG/click.svg";
import Swiper from "swiper";
export default {
  name: "DesktopNewUser",
  data: function() {
    return {
      jobType: "graduate"
    };
  },
  methods: {
    updateJobSelector: function() {
      const option1left = this.$refs.option1.getBoundingClientRect().left;
      const option2left = this.$refs.option2.getBoundingClientRect().left;
      if (this.jobType === "graduate") {
        this.$refs.jobTypeSelector.style.left = "0";
        this.$refs.pGraduate.style.color = "#FFFFFFff";
        this.$refs.pIntern.classList.add("p-blue");
      } else if (this.jobType === "intern") {
        this.$refs.jobTypeSelector.style.left = "50%";
        this.$refs.pIntern.classList.remove("p-blue");
        this.$refs.pIntern.style.color = "#FFFFFFff";
        this.$refs.pGraduate.style.color = "#6734e9";
      }
    }
  },
  mounted() {
    const mySwiper = new Swiper(".swiper-container", {
      effect: "coverflow",
      observer: true,
      observeParents: true,
      grabCursor: false,
      direction: "vertical",
      coverflowEffect: {
        rotate: 50,
        stretch: 0,
        depth: 100,
        modifier: 1,
        slideShadows: false
      },
      touchRatio: 0,
      keyboard: true,
      mousewheel: true,
      forceToAxis: true,
      navigation: {
        nextEl: ".swiper-button-next",
        prevEl: ".swiper-button-prev"
      },
      pagination: {
        el: ".swiper-pagination",
        type: "progressbar"
      }
    });
  }
};
</script>

<style lang="scss">
@import "../../../styles/_variables.scss";
.desktop-new-user {
  height: 90vh;
  width: 100%;

  &__slide {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: auto;

    & h1 {
      font-size: 4rem;
      width: 70%;
      margin: 2rem 0;
    }

    &__text-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      width: 40%;
      &__p {
        font-size: 2rem;
        color: $colour-grey-light;
        margin: 2rem 0;
      }
      &__input-wrapper {
        height: 6rem;
        width: 30rem;
        border: none;
        border-radius: 2.5rem;
        font-size: 2rem;
        border: 0.3rem solid $colour1;
        transition: all 0.5s;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        position: relative;
        margin: 2rem 0;
        &__radio-wrapper {
          width: 100%;
          height: 100%;
          display: flex;
        }
        &__selector {
          background-color: $colour1;
          height: 100%;
          position: absolute;
          width: 50%;
          border-radius: 2rem;
          left: 0;
          top: 0;
          transition: all 0.5s;
          @include gradientbg;
        }
        &__option {
          height: 100%;
          width: 50%;
          font-size: 2rem;
          border-radius: 2.5rem;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;
          &__radio-btn {
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: 10;
            opacity: 0;
            cursor: pointer;
          }
          & p {
            font-size: 2rem;
            text-align: center;
            color: $colour4;
          }
        }
        &__search-wrapper {
          display: flex;
          flex-direction: column;
          justify-content: space-evenly;
          &__input-box {
            display: flex;
            flex-direction: column;
            position: relative;
            margin-right: 2.5%;
            justify-content: space-evenly;
            margin: 2rem 0;
            width: 70%;
            & label {
              width: 100%;
              font-size: 2rem;
              padding: 1rem 2rem;
              margin-bottom: 1rem;
              position: absolute;
              opacity: 0;
              transition: all 0.5s;
              color: $colour4;
              border-top-left-radius: 25px;
              border-top-right-radius: 25px;
              background-color: $colour1;
              cursor: text;
            }
            &__input-search {
              height: 6rem;
              width: 100%;
              border-radius: 2.5rem;
              padding-left: 2rem;
              font-size: 2rem;
              border: 0.1rem solid $colour1;
              transition: all 0.5s;

              &:focus {
                border: 0.1rem solid $colour2;
              }
            }
            &__btn-search {
              @include gradientbg-yellow;
              width: 15rem;
              height: 6rem;
              font-size: 2rem;
              border-radius: 2.5rem;
              padding: 1rem 2rem;
              transition: all 0.2s;
              border: none;
              color: $colour1;
              cursor: pointer;
              border: solid 0.1rem transparent;
              overflow: hidden;
              position: relative;
              display: flex;
              align-items: center;
              justify-content: center;
              &:before {
                content: "";
                width: 100%;
                height: 100%;
                background-color: #fff;
                position: absolute;
                transition: all 0.2s ease;
                top: 0;
                left: 0;
                border-radius: 2rem;
                transform: translateX(-100%);
                transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
              }
              &:hover::before {
                transform: translateX(0);
              }
              &:hover {
                color: $colour1;
                border: solid 0.1rem $colour1;
              }
              &:hover p {
                color: $colour1;
              }
              & p {
                text-align: center;
                color: $colour1;
                font-size: 2rem;
                position: relative;
                transition: all 0.2s ease;
                z-index: 1;
              }
            }
          }
        }
      }
    }
  }
}

.p-blue {
  color: $colour1 !important;
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  height: 100%;
  overflow: auto;
  width: auto;
}

.swiper-pagination-progressbar-fill {
  background-color: $colour1;
}
</style>
