<template>
  <div class="desktop-landing-search">
    <h1 class="desktop-landing-search__header">The only job site built for students, by students.</h1>
    <!--<img src="../../../assets/SVG/education.svg" alt />-->
    <img src="../../../assets/SVG/students.png" alt="students" class="students-png" />
    <!---------------------------------------SEARCH WRAPPER--------------------------------------->
    <div class="desktop-landing-search__search-wrapper">
      <div class="desktop-landing-search__input-box">
        <div class="desktop-landing-search__job-type-radio">
          <div
            class="desktop-landing-search__job-type-radio__selector graduate-selector"
            ref="jobTypeSelector"
          ></div>
          <div
            class="desktop-landing-search__job-type-radio__option desktop-landing-search__job-type-radio__option__1"
            ref="option1"
          >
            <input
              type="radio"
              name="job-type"
              id="grad-type"
              class="desktop-landing-search__job-type-radio__option__radio-btn"
              value="graduate"
              v-model="jobType"
              v-on:change="updateJobSelector"
              checked
            />
            <p class ref="pGraduate">Graduate</p>
          </div>
          <div
            class="desktop-landing-search__job-type-radio__option desktop-landing-search__job-type-radio__option__2"
            ref="option2"
          >
            <input
              type="radio"
              name="job-type"
              id="intern-type"
              class="desktop-landing-search__job-type-radio__option__radio-btn"
              value="intern"
              v-model="jobType"
              v-on:change="updateJobSelector"
            />
            <p class="p-blue" ref="pIntern">Intern</p>
          </div>
        </div>
      </div>
      <div class="desktop-landing-search__input-box">
        <label for="inputIndustry" ref="industryLabel">e.g. Job title, Industry, Company</label>
        <input
          type="text"
          class="desktop-landing-search__input-box__input-search"
          id="inputIndustry"
          placeholder="What are you looking for?"
          ref="inputIndustry"
          @input="showLabel('industry')"
          v-model="inputIndustry"
        />
      </div>
      <div class="desktop-landing-search__input-box">
        <label for="inputLocation" ref="locationLabel">e.g. London, North West England</label>
        <input
          type="text"
          class="desktop-landing-search__input-box__input-search"
          id="inputLocation"
          placeholder="Location"
          ref="inputLocation"
          @input="showLabel('location')"
          v-model="inputLocation"
        />
        <i class="fa fa-map-pin fa-3x compass-icon"></i>
      </div>
      <div class="desktop-landing-search__input-box">
        <button class="desktop-landing-search__btn-search yellow-btn">
          <p>Search</p>
        </button>
      </div>
    </div>
    <!---------------------------------------ONE CLICK SEARCH WRAPPER--------------------------------------->
    <h2 class="desktop-landing-search__sub-header">Don't know what to search for? Try some of these:</h2>
    <div class="desktop-landing-search__one-click-wrapper">
      <div class="grid-container">
        <div class="grid-container__grid-item">
          <p>
            Engineering
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            Accountancy
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            Project Management
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            Copywriting
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            Teacher
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            North West
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            London
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            England
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
        <div class="grid-container__grid-item">
          <p>
            Scotland
            <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DesktopLandingSearch",
  data: function() {
    return {
      jobType: "graduate",
      inputIndustry: "",
      inputLocation: ""
    };
  },
  methods: {
    showLabel: function(input) {
      if (input === "industry" && this.inputIndustry !== "") {
        this.$refs.industryLabel.style.opacity = "1";
        this.$refs.industryLabel.style.top = "-80%";

        this.$refs.inputIndustry.style.borderTopLeftRadius = "0";
        this.$refs.inputIndustry.style.borderTopRightRadius = "0";
      } else if (input === "industry" && this.inputIndustry === "") {
        this.$refs.industryLabel.style.top = "0";
        this.$refs.industryLabel.style.opacity = "0";

        this.$refs.inputIndustry.style.borderTopLeftRadius = "2.5rem";
        this.$refs.inputIndustry.style.borderTopRightRadius = "2.5rem";
      } else if (input === "location" && this.inputLocation !== "") {
        this.$refs.locationLabel.style.opacity = "1";
        this.$refs.locationLabel.style.top = "-80%";

        this.$refs.inputLocation.style.borderTopLeftRadius = "0";
        this.$refs.inputLocation.style.borderTopRightRadius = "0";
      } else if (input === "location" && this.inputLocation === "") {
        this.$refs.locationLabel.style.top = "0";
        this.$refs.locationLabel.style.opacity = "0";

        this.$refs.inputLocation.style.borderTopLeftRadius = "2.5rem";
        this.$refs.inputLocation.style.borderTopRightRadius = "2.5rem";
      } else {
        null;
      }
    },
    updateJobSelector: function() {
      const option1left = this.$refs.option1.getBoundingClientRect().left;
      const option2left = this.$refs.option2.getBoundingClientRect().left;
      if (this.jobType === "graduate") {
        this.$refs.jobTypeSelector.style.left = "0";
        this.$refs.pIntern.style.color = "#59595a";
        this.$refs.pGraduate.style.color = "#0456DA";
      } else if (this.jobType === "intern") {
        this.$refs.jobTypeSelector.style.left = "50%";
        this.$refs.pIntern.classList.remove("p-blue");
        this.$refs.pIntern.style.color = "#0456DA";
        this.$refs.pGraduate.style.color = "#59595a";
      }
    }
  }
};
</script>

<style lang="scss">
@import "../../../styles/_variables.scss";
.desktop-landing-search {
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  &__header {
    font-size: 5rem;
    border-radius: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $colour-grey;
    z-index: 10;
    text-align: center;
  }
  &__sub-header {
    font-size: 2.5rem;
    border-radius: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $colour1;
    z-index: 10;
    text-align: center;
  }
  &__search-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 6rem;
    height: 10vh;
    z-index: 10;
  }
  &__one-click-wrapper {
    width: 100%;
    margin: 3rem;
    height: 10vh;
    display: flex;
    justify-content: center;
  }
  &__input-box {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-right: 2.5%;
    justify-content: center;
    height: 6rem;
    & label {
      width: 100%;
      font-size: 2rem;
      padding: 1rem 2rem;
      margin-bottom: 1rem;
      position: absolute;
      opacity: 0;
      transition: all 0.5s;
      color: $colour4;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
      background-color: $colour1;
      cursor: text;
    }
    &__input-search {
      height: 100%;
      width: 100%;
      border-radius: 2.5rem;
      padding-left: 2rem;
      font-size: 2rem;
      border: 0.2rem solid $colour1;
      transition: all 0.5s;
      min-width: 40rem;

      &:focus {
        border: 0.2rem solid $colour2;
      }
    }
  }
  &__job-type-radio {
    height: 6rem;
    width: 30rem;
    border: none;
    border-radius: 2.5rem;
    font-size: 2rem;
    border: 0.3rem solid $colour-grey-light;
    transition: all 0.5s;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    &:focus {
      border: 0.2rem solid $colour-grey !important;
    }
    &__selector {
      background-color: $colour4;
      border: 0.4rem solid $colour1;
      height: 100%;
      position: absolute;
      width: 50%;
      border-radius: 2.5rem;
      left: 0;
      top: 0;
      transition: all cubic-bezier(0.23, 1, 0.32, 1) 0.5s;
    }
    &__option {
      height: 100%;
      width: 50%;
      font-size: 2rem;
      border-radius: 2.5rem;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      &__radio-btn {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 10;
        opacity: 0;
        cursor: pointer;
      }
      & p {
        font-size: 2rem;
        text-align: center;
        color: $colour1;
      }
    }
  }
  &__one-click-box {
    @include gradientbg;
    cursor: pointer;
    height: 12rem;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 2.5% 1%;
    padding: 4rem;
    border: 0.2rem solid transparent;
    transition: all 0.2s;
    &__p-info {
      font-size: 2rem;
      color: $colour4;
      text-align: center;
    }
    &:hover {
      background: transparent;
      border: 0.2rem solid $colour1;
      -webkit-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      -moz-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      transform: translateY(-1rem);
    }
    &:active {
      background: transparent;
      border: 0.2rem solid $colour1;
      -webkit-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      -moz-box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      box-shadow: 0px 30px 20px -21px rgba(92, 116, 156, 0.5);
      transform: translateY(0);
    }
    &:hover p {
      color: $colour1;
    }
  }
  &__btn-search {
    @include gradientbg;
    width: 15rem;
    height: 6rem;
    font-size: 2rem;
    border-radius: 2.5rem;
    padding: 1rem 2rem;
    transition: all 0.2s;
    border: none;
    color: $colour4;
    cursor: pointer;
    border: solid 0.2rem transparent;
    overflow: hidden;
    position: relative;
    &:before {
      content: "";
      width: 100%;
      height: 100%;
      background-color: #fff;
      position: absolute;
      transition: all 0.2s ease;
      top: 0;
      left: 0;
      border-radius: 2.5rem;
      transform: translateX(-110%);
      transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
    }
    &:hover::before {
      transform: translateX(0);
    }
    &:hover {
      color: $colour1;
      border: solid 0.2rem $colour1;
    }
    &:hover p {
      color: $colour1;
    }
    & p {
      text-align: center;
      color: $colour4;
      font-size: 2rem;
      position: relative;
      transition: all 0.2s ease;
      z-index: 1;
    }
  }
}

.compass-icon {
  position: absolute;
  right: 5%;
  color: $colour1;
  cursor: pointer;
}

.p-blue {
  color: $colour1 !important;
}

.search-svg {
  position: absolute;
  width: 25%;
  right: 5%;
  bottom: 5%;
  opacity: 0.3;
  pointer-events: none;
}

.career-svg {
  position: absolute;
  width: 50%;
  left: 2.5%;
  top: 20%;
  opacity: 0.3;
  pointer-events: none;
}

.grid-container {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap;

  &__grid-item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem;

    &__plus-icon {
      font-size: 2rem;
    }

    & p {
      font-size: 2rem;
      border: $colour-grey-light 0.2rem solid;
      border-radius: 2rem;
      padding: 1rem 2rem;
      text-align: center;
      color: $colour-grey;
      cursor: pointer;
      transition: all 0.2s;
      &:hover {
        background-color: $colour-yellow;
        color: $colour2;
      }
    }
  }
}

.students-png {
  position: fixed;
  height: 50%;
  bottom: -26%;
}

.yellow-btn {
  @include gradientbg-yellow;
  & p {
    color: $colour1;
  }
}
</style>
