import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";
import Landing from "@/views/Landing.vue";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "landing",
    component: Landing
  },
  {
    path: "/index.html",
    redirect: "/"
  }
];

const router = new VueRouter({
  routes
});

export default router;
