<template>
  <div class="desktop-landing-video">
    <video src="../../../assets/video.mp4" autoplay muted loop id="backgroundVideo"></video>
    <div id="video-overlay"></div>
    <transition name="menu-popover">
      <div class="desktop-landing-video__container" ref="box">
        <h1>What if there was a website specifically for graduates and interns?</h1>
        <h2>Well, now there is.</h2>
        <h3>Go ahead, give it a try:</h3>
        <div class="desktop-landing-video__container__input-box">
          <div class="desktop-landing-video__container__input-box__job-type-radio">
            <div
              class="desktop-landing-video__container__input-box__job-type-radio__selector graduate-selector"
              ref="jobTypeSelector"
            ></div>
            <div
              class="desktop-landing-video__container__input-box__job-type-radio__option desktop-landing-video__container__input-box__job-type-radio__option__1"
              ref="option1"
            >
              <input
                type="radio"
                name="job-type"
                id="grad-type"
                class="desktop-landing-video__container__input-box__job-type-radio__option__radio-btn"
                value="graduate"
                v-model="jobType"
                v-on:change="updateJobSelector"
                checked
              />
              <p class ref="pGraduate">Graduate</p>
            </div>
            <div
              class="desktop-landing-video__container__input-box__job-type-radio__option desktop-landing-video__container__input-box__job-type-radio__option__2"
              ref="option2"
            >
              <input
                type="radio"
                name="job-type"
                id="intern-type"
                class="desktop-landing-video__container__input-box__job-type-radio__option__radio-btn"
                value="intern"
                v-model="jobType"
                v-on:change="updateJobSelector"
              />
              <p class="p-blue" ref="pIntern">Intern</p>
            </div>
          </div>
        </div>
        <div class="desktop-landing-video__container__input-box">
          <input
            type="text"
            class="desktop-landing-video__container__input-box__input-search"
            id="inputIndustry"
            placeholder="What are you looking for?"
            ref="inputIndustry"
            @input="showLabel('industry')"
            v-model="inputIndustry"
          />
        </div>
        <div class="desktop-landing-video__container__input-box">
          <input
            type="text"
            class="desktop-landing-video__container__input-box__input-search"
            id="inputLocation"
            placeholder="Location"
            ref="inputLocation"
            @input="showLabel('location')"
            v-model="inputLocation"
          />
          <i class="fa fa-map-pin fa-3x compass-icon"></i>
        </div>
        <div class="desktop-landing-video__container__one-click-box">
          <h2 class="desktop-landing-video__container__one-click-box__h2">
            Don't know what to search for?
            <br />Try some of these:
          </h2>
          <div class="grid-container">
            <div class="grid-container__grid-item">
              <p>
                Engineering
                <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
              </p>
            </div>
            <div class="grid-container__grid-item">
              <p>
                Accountancy
                <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
              </p>
            </div>
            <div class="grid-container__grid-item">
              <p>
                Project Management
                <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
              </p>
            </div>
            <div class="grid-container__grid-item">
              <p>
                Copywriting
                <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
              </p>
            </div>
            <div class="grid-container__grid-item">
              <p>
                Teacher
                <i class="fa fa-plus grid-container__grid-item__plus-icon"></i>
              </p>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
// typical import
import { gsap } from "gsap/dist/gsap";
import { TimelineLite } from "gsap/dist/gsap";
export default {
  Name: "DesktopLandingVideo",
  data: function() {
    return {
      jobType: "graduate",
      inputIndustry: "",
      inputLocation: ""
    };
  },
  mounted() {
    const { box } = this.$refs;
    const timeline = new TimelineLite();
    timeline.to(box, 1, { x: 0 });
  },
  methods: {
    showLabel: function(input) {
      if (input === "industry" && this.inputIndustry !== "") {
        this.$refs.industryLabel.style.opacity = "1";
        this.$refs.industryLabel.style.top = "-80%";

        this.$refs.inputIndustry.style.borderTopLeftRadius = "0";
        this.$refs.inputIndustry.style.borderTopRightRadius = "0";
      } else if (input === "industry" && this.inputIndustry === "") {
        this.$refs.industryLabel.style.top = "0";
        this.$refs.industryLabel.style.opacity = "0";

        this.$refs.inputIndustry.style.borderTopLeftRadius = "2.5rem";
        this.$refs.inputIndustry.style.borderTopRightRadius = "2.5rem";
      } else if (input === "location" && this.inputLocation !== "") {
        this.$refs.locationLabel.style.opacity = "1";
        this.$refs.locationLabel.style.top = "-80%";

        this.$refs.inputLocation.style.borderTopLeftRadius = "0";
        this.$refs.inputLocation.style.borderTopRightRadius = "0";
      } else if (input === "location" && this.inputLocation === "") {
        this.$refs.locationLabel.style.top = "0";
        this.$refs.locationLabel.style.opacity = "0";

        this.$refs.inputLocation.style.borderTopLeftRadius = "2.5rem";
        this.$refs.inputLocation.style.borderTopRightRadius = "2.5rem";
      } else {
        null;
      }
    },
    updateJobSelector: function() {
      const option1left = this.$refs.option1.getBoundingClientRect().left;
      const option2left = this.$refs.option2.getBoundingClientRect().left;
      if (this.jobType === "graduate") {
        this.$refs.jobTypeSelector.style.left = "0";
        this.$refs.pIntern.style.color = "#59595a";
        this.$refs.pGraduate.style.color = "#0456DA";
      } else if (this.jobType === "intern") {
        this.$refs.jobTypeSelector.style.left = "50%";
        this.$refs.pIntern.classList.remove("p-blue");
        this.$refs.pIntern.style.color = "#0456DA";
        this.$refs.pGraduate.style.color = "#59595a";
      }
    }
  }
};
</script>

<style lang="scss">
@import "../../../styles/_variables.scss";
.desktop-landing-video {
  width: 100%;
  height: 100%;
  display: flex;

  &__container {
    transform: translateX(-300%);
    transition: all cubic-bezier(0.23, 1, 0.32, 1);
    @include gradientbg;
    height: 70%;
    width: 50%;
    margin: 5% 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 2rem;
    overflow: visible;
    position: relative;
    & h1 {
      font-size: 4.2rem;
      color: $colour4;
    }
    & h2 {
      font-size: 3.2rem;
      color: $colour-yellow;
    }
    & h3 {
      font-size: 2.2rem;
      color: $colour4;
    }
    &__input-box {
      &__input-search {
        position: relative;
        height: 6rem;
        width: 50%;
        border-radius: 2.5rem;
        padding-left: 2rem;
        font-size: 2rem;
        border: 0.2rem solid $colour1;
        transition: all 0.5s;
        min-width: 40rem;

        &:focus {
          border: 0.2rem solid $colour2;
        }
      }
      &__job-type-radio {
        height: 6rem;
        width: 50%;
        border: none;
        border-radius: 2.5rem;
        font-size: 2rem;
        border: 0.2rem solid $colour1;
        transition: all 0.5s;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        position: relative;
        background-color: $colour4;
        &:focus {
          border: 0.2rem solid $colour-grey !important;
        }
        &__selector {
          background-color: $colour4;
          border: 0.4rem solid $colour3;
          height: 100%;
          position: absolute;
          width: 50%;
          border-radius: 2rem;
          left: 0;
          top: 0;
          transition: all cubic-bezier(0.23, 1, 0.32, 1) 0.5s;
        }
        &__option {
          height: 100%;
          width: 50%;
          font-size: 2rem;
          border-radius: 2.5rem;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;
          &__radio-btn {
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: 10;
            opacity: 0;
            cursor: pointer;
          }
          & p {
            font-size: 2rem;
            text-align: center;
            color: $colour1;
          }
        }
      }
    }
    &__one-click-box {
      position: absolute;
      height: 60%;
      width: 50%;
      background-color: $colour4;
      right: -25%;
      top: 30%;
      padding: 1rem;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;

      &__h2 {
        font-size: 2rem !important;
        color: $colour1 !important;
      }
    }
  }
}

.grid-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap;

  &__grid-item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 1rem;

    &__plus-icon {
      font-size: 2rem;
    }

    & p {
      font-size: 2rem;
      border: $colour-grey-light 0.2rem solid;
      border-radius: 2rem;
      padding: 1rem 2rem;
      text-align: center;
      color: $colour-grey;
      cursor: pointer;
      transition: all 0.2s;
      &:hover {
        background-color: $colour-yellow;
        color: $colour2;
      }
    }
  }
}

.box {
  width: 100px;
  height: 100px;
  background: #54e365;
  z-index: 10;
}

.compass-icon {
  position: relative;
  right: 5%;
  color: $colour1;
  cursor: pointer;
}

#backgroundSprite {
  position: fixed;
  width: 60%;
  height: 160%;
  top: -10%;
  left: -30%;
  z-index: -3;
  overflow: hidden;
}

#navSprite {
  position: fixed;
  width: 40%;
  height: 15%;
  top: 0;
  right: 00;
  z-index: -3;
  overflow: hidden;
}

#video-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: $colour4;
  opacity: 0.5;
  z-index: -9;
}

#backgroundVideo {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -10;
}
</style>